import React, {useState} from "react";
import NavBar from "../components/Navbar";
import MobileNavbar from "../components/MobileNavbar";
import './About.css';
import GroupPhoto from '../pictures/GroupPhoto.jpg';

function About() {

    const [hovered, setHovered] = useState(false);
    const toggleHover = () => setHovered(!hovered);

    return (
        <>
        <NavBar />
            <MobileNavbar />
                <section id="about">
                    <div className='aboutWrapper'
                        onMouseEnter={toggleHover} 
                        onMouseLeave={toggleHover}>
                        <img className='groupPhoto' src={GroupPhoto} alt="Logo" />
                            <span className="hoverMe"> Zadri </span>
                                <h2 className={hovered ? 'aboutDescriptionOpen' : 'aboutDescriptionClosed'}>
                                    Skhizma - Autorski bend iz Prijedora koji je nastao 2021 godine.<br />
                                    Sastav ine:<br />
                                    Ivan Sanianin  bas<br />
                                    Dejan Mandi  bubanj<br />
                                    Djordje Vasi  gitara, vokal<br /><br />

                                    Sve izdate pjesme su snimljene u
                                    <a className='cirithUngol' 
                                        href='https://www.facebook.com/cirithungolstudio' 
                                        target="_blank" rel="noreferrer"> Cirith Ungol Studio. </a>

                                    Volimo da eksperimentiemo, jer mnogi muziki pravci i bendovi utiu na nas. 
                                    Svoj muziki stil bi opisali kao rock, metal i funk.

                                    Teimo ka to skorijoj realizaciji albuma. 
                                    Imamo jo spremnog materijala kojeg emo poeti snimati nadamo se ubrzo. 
                                    Mi emo se truditi da to bude to bolje i nadamo se da e sve tei kako treba.
                                    - izjavili smo za
                                    <a className='highwaystarMagazine' 
                                        href='https://highwaystarmagazine.org' 
                                        target="_blank" rel="noreferrer"> Highwaystar Magazin.</a>
                                </h2>
                    </div>
                </section>
        </>
    )
}

export default About;